*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
  background: linear-gradient(to right, #38393f, #b63a46, #38393f);
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  text-align: center;
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}

.cardGrid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Titulo{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: xx-large;
  font-style: oblique;
  text-align: center;
}